// rating component

.rating {
    padding: 60px 0;
    background: $backgroundHighlight;

    &_list {
        &-item {
            text-align: center;
            margin-bottom: 40px;

            &:last-of-type {
                margin-bottom: 0;
            }

            .main {
                margin-bottom: 20px;
                &_rating {
                    margin-bottom: 5px;
                }
            }

            .media {
                height: 32px;
                overflow: visible;
            }
        }
    }
}

// media queries

@media screen and (min-width: 575.98px) {
    .rating {
        &_list {
            &-item {

                &[data-order="1"] {
                    order: 1;
                }

                &[data-order="2"] {
                    order: 3;
                    margin-bottom: 0;
                }

                &[data-order="3"] {
                    margin-bottom: 0;
                    order: 2;
                }

                &[data-order="4"] {
                    order: 4;
                }
            }
        }
    }
}

@media screen and (min-width: 1199.98px) {
    .rating {
        &_list {
            &-item {

                &.col-xl-3 {
                    margin-bottom: 0;
                    order: unset !important;
                    text-align: left;
                }
            }
        }
    }
}